import React from 'react'
import {Link} from 'react-router-dom'
import { Container, Button } from '../../globalStyles'
import {InfoSec, 
    InfoColumn,
    InfoRow,
    TextWrapper,
    TopLine,
    Heading,
    Subtitle, 
    ImgWrapper,
    Img   
} from './InfoSection.elements'

export default function InfoSection({primary, lightBg, imgStart, lightTopLine, lightTextDesc, topLine, lightText, headline, description, buttonLabel, img, alt, start}){
    return(
        <>
            <InfoSec lightBg={lightBg}>
                <Container>
                    <InfoRow imgStart={imgStart}>
                        <InfoColumn>
                            <TextWrapper>
                                <TopLine lightTopLine={lightTopLine}>{topLine}</TopLine>
                                <Heading lightText={lightText}>{headline}</Heading>
                                <Subtitle lightTextDesc={lightTextDesc}>{description}</Subtitle>
                                <Link to='/sign-up>'>
                                    <Button big fontBig primary={primary}>
                                        {buttonLabel}
                                    </Button>
                                </Link>
                            </TextWrapper>
                        </InfoColumn>
                        <InfoColumn>
                            <ImgWrapper start={start}>
                                <Img src={img} alt={alt} />
                            </ImgWrapper>
                        </InfoColumn>
                    </InfoRow> 
                </Container>
            </InfoSec>
        </>
    )
}